{ ref, computed } from \"vue\";\n    import { IUser_SUSI } from \"../../../../../../../common/qCloudTemp/authentication\";\n    import { container } from \"../../../../../domain/core/diContainer\";\n    import { IBackofficeUseCase } from \"../../../../../domain/useCase/IBackofficeUseCase\";\n    import { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\n    import { IViewModel } from \"../../../../../domain/viewModel/IViewModel\";\n\n    interface IManageUserData {\n        user: IUser_SUSI,\n    }\n    const executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const backofficeUseCase = container.resolve<IBackofficeUseCase>(IBackofficeUseCase);\n    const viewModel = container.resolve<IViewModel>(IViewModel);\n\n    const props = defineProps<IManageUserData>()\n    const user = ref(props.user);\n    const createOrgLimit = ref(user.value.limits?.createOrg);\n    const emit = defineEmits([\"close\"]);\n\n    function close() {\n        emit(\"close\");\n    }\n    function save() {\n        executor.execute(async () => {\n            const updatedUser = await backofficeUseCase.updateUserInfo(user.value.id, {\n                createOrgLimit: createOrgLimit.value!\n            });       \n            user.value.limits!.createOrg = createOrgLimit.value;\n            user.value = updatedUser;\n            \n        }, { loading: true });\n        close();\n    }\n</script>\n<template>\n    <div class=\"container\">\n        <div class=\"org-limit\">\n            <span>Number of organizations that can be created</span>\n            <input type=\"number\" v-model=\"createOrgLimit\"/>\n        </div>\n        <div class=\"buttons-div\">\n            <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n            <button class=\"save-button\" @click=\"save()\">SAVE</button>\n        </div>\n    </div>\n</template>\n<style scoped>\n* {\n    font-family: \"Roboto\";\n}\n .container {\n        padding: 32px 20px 20px;\